




























































































import { Component, Vue } from 'vue-property-decorator';

@Component({
  components: {},
})
export default class About extends Vue {
  valid = false;
  loading = false;

  types = ['New Build', 'Small Renovation Interior', 'Large Renovation Interior', 'Furnishings Design/Decorating'];

  request = {
    firstName: '',
    lastName: '',
    email: '',
    phone: '',
    selectedType: null
  }

  contactUs () {
    this.loading = true;

    setTimeout(() => {
      this.loading = false;
      this.request = {
        firstName: '',
        lastName: '',
        email: '',
        phone: '',
        selectedType: null
      }
    }, 4000)
  }
}
